.App {
  width: 100vw;
    height: 100vh;
    font-family: "Roboto", sans-serif;
    display: flex;
    justify-content: center;
      flex-direction: column;
      align-items: center;
      border-radius: 5px;
      text-align: center;
   
}


  #quiz {
    height: 50vh;
    width: 100vw;
    display: flex;
  justify-content: center;
    flex-direction: column;
    align-items: center;
    background-color: #ffde00;
    border-radius: 5px;
    text-align: center;
  }

  #scoreboard {
    position: absolute;
  margin-left: 85vw;
  margin-top: 40vh;
    padding: 5px;
    z-index: 1;
  }


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
